<template>
  <div class="container">
    <el-row type="flex">
      <el-col :span="4">
        <div class="left_container">
          <div class="logo">
            <img :src="img_logo" alt="logo">
          </div>
        </div>
      </el-col>
      <el-col :span="20">
        <el-row type="flex" justify="center">
          <el-col :span="24">
          </el-col>
          <el-col>
            <el-menu :default-active="activeIndex" mode="horizontal" @select="handleSelect" background-color="#545c64" text-color="#fff" active-text-color="#ffd04b">
              <el-menu-item index="1">首页</el-menu-item>
              <el-menu-item index="2">服务</el-menu-item>
              <el-menu-item index="3">工作清单</el-menu-item>
              <el-menu-item index="4" disabled>服务流程</el-menu-item>
              <el-menu-item index="5">联系我们</el-menu-item>
            </el-menu>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import img_logo from '@/assets/logo/logo_color_120x44dp.png'

export default {
  data() {
    return {
      img_logo,
      activeIndex: '1'
    };
  },
  methods: {
    handleSelect(key, keyPath) {
      console.log(key, keyPath);
    }
  }
}
</script>

<style scoped>
.container {
  background: #545c64;
}

.left_container {
  display: flex;
  height: 100%;
}

.logo {
  margin-left: 8px;
  display: flex;
  flex-direction: column-reverse;
}

.logo img {
  width: 120px;
  height: 44px;
}

.el-menu{
   padding-right: 0px;
}

</style>


